<template>
  <div class="container py-5">
    <NavTabs />
    <Spinner v-if="isLoading" />
    <template v-else>
      <RestaurantsNavPills :categories="categories" />
      <!-- 這裡不在父元件就用 v-for的方式將categories傳入RestaurantsNavPills
      我猜是因為有"全部"這個分類的寫法跟其他分類不太一樣所以選擇在RestaurantsNavPills元件內才用v-for迭帶-->
      <div class="row">
        <RestaurantCard
          v-for="restaurant in restaurants"
          :key="restaurant.id"
          :initial-restaurant="restaurant"
        />
        <!--  傳入的參數不命名為restaurant而改為initial-restaurant是因為由父元件傳給子元件的porps屬性是不可於子元件內更改的,而我們在子元件上需要修改isFavorited,isLiked 所以我們必須在RestaurantCard元件內建立屬於子元件本身的data屬性,如此才可做修改-->
      </div>
      <div v-if="restaurants.length < 1">此類別目前無餐廳資料</div>
      <RestaurantsPagination
        v-if="totalPage.length > 1"
        :category-id="categoryId"
        :current-page="currentPage"
        :total-page="totalPage"
        :previous-page="previousPage"
        :next-page="nextPage"
      />
    </template>
  </div>
</template>

<script>
import NavTabs from "../components/NavTabs";
import RestaurantCard from "../components/RestaurantCard";
import RestaurantsNavPills from "../components/RestaurantsNavPills";
import RestaurantsPagination from "../components/RestaurantsPagination";
import restaurantsAPI from "../apis/restaurants";
import { Toast } from "../utils/helpers";
import Spinner from "../components/Spinner";

export default {
  data() {
    return {
      restaurants: [],
      categories: [],
      categoryId: -1,
      currentPage: 1,
      totalPage: [],
      previousPage: -1,
      nextPage: -1,
      isLoading: true
    };
  },
  methods: {
    async fetchRestaurants({ page, categoryId }) {
      try {
        const response = await restaurantsAPI.getRestaurants({
          page,
          categoryId
        });

        this.restaurants = response.data.restaurants;
        this.categories = response.data.categories;
        this.categoryId = response.data.categoryId;
        this.currentPage = response.data.page;
        this.totalPage = response.data.totalPage;
        this.previousPage = response.data.prev;
        this.nextPage = response.data.next;

        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        Toast.fire({
          icon: "error",
          title: "無法取得餐廳資料，請稍後再試"
        });
      }
    }
  },
  /*
  created()只有重新 render <router-view /> 的時候才會觸發,
  當使用者切換分類或頁數的時候,(或是直接修改網址的params),因為是link to同一個Restaurants.vue
  所以頁面並沒有重新render,也就不會觸發created(),也不會呼叫fetchRestaurants
  因此切換分類或頁數我們需要另一個在「網址產生變化時」會被觸發的方法---->beforeRouteUpdate
  */
  created() {
    const { page = "", categoryId = "" } = this.$route.query;
    this.fetchRestaurants({ page, categoryId });
  },
  // 使用 beforeRouteUpdate 方法取得使用者路由變化
  beforeRouteUpdate(to, from, next) {
    //當query string為空時,to.query.page or to.query.categoryId為"undefined",傳入後端會造成錯誤,所以給空字串為預設值
    const { page = "", categoryId = "" } = to.query;
    this.fetchRestaurants({ page, categoryId });
    next();
  },
  components: {
    NavTabs: NavTabs,
    RestaurantCard: RestaurantCard,
    RestaurantsNavPills: RestaurantsNavPills,
    RestaurantsPagination,
    Spinner
  }
};
</script>
